.cdfooter {
  position: relative;
  background: #252d43;
  padding: 27px 0px;
}

.cdcontainer {
  max-width: 1150px;
  margin: 0px auto;
  padding: 0px 15px;
}

.cdfooter p {
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  letter-spacing: -0.01em;
  color: #9b9b9b;
  margin: 0;
  font-weight: 500;
}

.cdfooter a {
  text-decoration: none;
  color: #9b9b9b;
  transition: all 0.3s;
}
.cdfooterPay {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 0;
  gap: 10px;
}
.cdfooterDetail {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.cdfooterDetail span {
  font-weight: 700;
}

@media screen and (max-width: 767px) {
  .cdfooter {
    padding: 49px 0px;
  }
}
