
.errortoast {
    background: #d93355;
    box-shadow: 10px 10px 60px #1b1d23;
    border-radius: 12px;
    position: fixed;
    right: 0px;
    top: 20px;
    max-width: 420px;
    width: 100%;
    /* -webkit-animation: slide 0.5s forwards;
    -webkit-animation-delay: 0.2s;
    animation: slide 0.5s forwards;
    animation-delay: 0.2s; */
    visibility: hidden;
    opacity: 1;
    z-index: 9999;
  }
  
  .errortoastgrid {
    padding: 16px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 60px;
  }
  
  .errortoasticon {
    width: 24px;
  }
  
  .errortoasticon img {
    width: 100%;
  }
  
  .errortoastcontent {
    width: calc(100% - 34px);
  }
  
  .errortoastcontent p {
    padding: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
    margin: 0;
  }
  
  .errortoastclose {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }


