.sidebar {
  position: relative;
  max-width: 316px;
  width: 100%;
  background: #f5f8f8;
  border-radius: 10px;
  margin-top: 35px;
}

.report {
  width: calc(100% - 336px);
}

.sidebarGrid p {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  margin: 0;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  padding-bottom: 15px;
  color: #333333;
  position: absolute;
  top: -40px;
  left: 0;
}

.sidebarGrid ul {
  margin: 0;
  padding: 20px;
  list-style-type: none;
}

.sidebarGrid ul li span {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 150% */
  letter-spacing: -0.01em;
  font-feature-settings: "calt" off;
  color: #333333;
  display: flex;
  align-items: center;
  padding-bottom: 5px;
}

.sidebarGrid ul li span img {
  margin-left: 10px;
}

.sidebarGrid ul li b img {
  margin-left: 10px;
  display: none;
}

.sidebarGrid ul li {
  margin-bottom: 10px;
}

.sidebarGrid ul li b {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  color: #333333;
}
.sidebarGrid ul li b .mobileview {
  display: none;
}
.resubmit {
  padding: 0px 20px 10px;
}

.resubmit a {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  display: block;
  color: #f35c5c;
  text-decoration: none;
  transition: all 0.3s;
}

.resubmit a:hover {
  color: #6243ce;
}

.reportBox,
.report {
  position: relative;
}

.reportGrid p {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  margin: 0;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  padding-bottom: 15px;
  color: #333333;
}

.reportCard {
  background: #ffffff;
  box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 15px;
  position: relative;
}

.reportCardBlurred::before {
  content: "";
  background: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(3.5px);
  border-radius: 10px;
  width: 100%;
  height: 125px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
}

.reportBox .reportCard:last-child {
  margin: 0;
}

.reportCardTitle h4 {
  font-weight: 400;
  font-size: 26px;
  line-height: 120%;
  padding-bottom: 10px;
  letter-spacing: 0.01em;
  margin: 0;
  color: #333333;
}

.reportCardTitle span {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  margin: 0;
  letter-spacing: -0.01em;
  padding-bottom: 5px;
  color: #333333;
  display: block;
}

.reportGrid .reportCardTitle p {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  padding: 15px 0px 20px;
  color: #333333;
  margin: 0;
  text-transform: none;
}

.reportLoaderBody {
  max-width: 240px;
  height: 17px;
  background: #ced0d0;
  border-radius: 30px;
  overflow: hidden;
  position: relative;
}

.reportLoaderWrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 17px;
  background: #2cd674;
  box-shadow: 0px 3px 6px rgba(44, 214, 116, 0.36);
  position: relative;
}

.reportLoader {
  position: absolute;
  width: 2px;
  height: 100%;
  background: #fff;
}

.reportLoader.loader1 {
  left: 20%;
}

.reportLoader.loader2 {
  left: 40%;
}

.reportLoader.loader3 {
  left: 60%;
}

.reportLoader.loader4 {
  left: 80%;
}

.reportLoaderWrapperContent {
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: start;
  justify-content: flex-start;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.reportLoaderWrapper:after {
  background: url(../../assets/results/checkicon.svg);
  width: 12px;
  height: 9px;
  content: "";
  display: block;
  background-size: cover;
  position: absolute;
  right: 16px;
  top: 3px;
}

