.reportCardFooter {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.reportCardFooter p {
  background: rgba(206, 208, 208, 0.4);
  border-radius: 6px;
  padding: 6px;
  width: calc(100% - 240px);
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  margin: 0;
  letter-spacing: -0.01em;
  color: #333333;
  text-transform: none;
}

.reportCardFooter a {
  max-width: 152px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #f35c5c;
  border-radius: 74px;
  padding: 10px 20px;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  color: #f35c5c;
  text-decoration: none;
  transition: all 0.3s;
  cursor: pointer;
}

.reportCardFooter a:hover {
  border-color: #6243ce;
  color: #6243ce;
}

.formContainer {
  max-width: 962px;
  margin: 0px auto;
}

.formTitle h3 {
  margin: 0;
  font-weight: 400;
  font-size: 26px;
  line-height: 120%;
  letter-spacing: 0.01em;
  padding-bottom: 22px;
  color: #6243ce;
  display: flex;
  align-items: center;
  justify-content: center;
}

.formTitle h3 img {
  margin-right: 20px;
}
.formTitle p {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  letter-spacing: -0.01em;
  color: #333333;
  padding-bottom: 40px;
}

.formGrid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.formBox {
  max-width: 394px;
  width: 100%;
  padding: 40px 30px;
  background: #ffffff;
  border: 2px solid rgba(98, 67, 206, 0.18);
  box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
  border-radius: 10px;
}

.formBox form {
  position: relative;
}

.formBox input {
  background: #ffffff;
  border: 2px solid #eee9ff;
  border-radius: 51px;
  margin-bottom: 10px;
  width: 100%;
  padding: 9px 20px;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: -0.01em;
  box-sizing: border-box;
  font-family: "Raleway", sans-serif;
}

.formBox input:focus {
  outline: none;
}

.formBox input::placeholder {
  color: #6e6f6f;
}

.formBox button {
  background: #f35c5c;
  border-radius: 63px;
  padding: 19px 20px;
  width: 100%;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  border: 1px solid #f35c5c;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}

.formBox button:hover {
  background: #6243ce;
  border-color: #6243ce;
}

.formBox button img {
  width: 25px;
}

.formBox label {
  font-weight: 400;
  font-size: 13px;
  line-height: 140%;
  display: flex;
  color: #727c88;
  margin-top: 10px;
  letter-spacing: 0.5px;
}

.formBox label span a {
  color: #727c88;
}

.formBox input[type="checkbox"] {
  width: 18px;
  height: 18px;
  margin: 0;
  margin-right: 15px;
  border: 1px solid #6243ce;
  cursor: pointer;
}

.formContent {
  position: relative;
  width: calc(100% - 520px);
}

.formContent ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 28px;
}

.formContent ul li {
  font-weight: 700;
  font-size: 20px;
  line-height: 172.5%;
  letter-spacing: -0.01em;
  position: relative;
  color: #333333;
  padding-left: 35px;
}

.formContent ul li::before {
  content: "";
  background: url(../../assets/results/check1.webp);
  width: 22px;
  height: 18px;
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-size: cover;
}

.formFieldTel {
  padding-left: 45px !important;
}

.loader-icon {
  width: 25px;
}
/* ****************** Error msg ****************** */

.formField.errorMsg {
  border-color: #f36f6f;
}

/****************** Media Query *********************/

@media screen and (max-width: 991px) {
  .formContent {
    width: calc(100% - 400px);
  }
  .formBox {
    max-width: 300px;
  }

  .sidebar {
    max-width: 100%;

    margin-bottom: 21px;
  }
  .report {
    width: 100%;
  }

  .reportCardFooter p {
    width: 100%;
    margin-bottom: 10px;
  }

  .results {
    padding-top: 24px;
  }

  .pageTitle h2 {
    font-size: 30px;
    line-height: 120%;
    justify-content: center;
  }

  .sidebarGrid ul li span {
    display: none;
  }

  .sidebarGrid ul li b {
    font-weight: 700;
    font-size: 14px;
    line-height: 120%;
    display: flex;
    flex-wrap: wrap;
    gap: 3px;
  }

  .sidebarGrid ul {
    padding: 20px 20px 0px;
  }

  .reportCardTitle h4 {
    font-size: 22px;
    line-height: 120%;
  }
  .reportGrid .reportCardTitle p {
    padding: 15px 0px 10px;
  }

  .reportCardFooter a {
    max-width: 100%;
  }

  .formTitle h3 {
    flex-direction: column;
    text-align: center;
    gap: 10px;
    font-size: 30px;
    line-height: 120%;
  }

  .formTitle h3 img {
    margin: 0;
  }

  .formTitle p {
    padding-bottom: 30px;
  }
  .formContent ul {
    gap: 10px;
  }

  .sidebarGrid ul li b img {
    display: inline;
  }
  .reportCardBlurred::before {
    height: 140px;
  }
  .sidebarGrid ul li b .mobileview {
    display: block;
  }
  .desktopview{
    display: none;
  }
}

@media screen and (max-width: 767px) {
  .formGrid {
    gap: 30px;
  }

  .formBox {
    max-width: 100%;
    margin: 0px auto;
    padding: 30px 15px;
    order: 2;
  }
  .formContent {
    width: 100%;
    order: 1;
  }
  .reportCardBlurred::before {
    height: 200px;
  }
}
