.topBanner {
  padding: 66px 0px 112px;
  position: relative;
}

.error-msg .react-select__control {
  border: 2px solid red;
}

.topBanner:before {
  content: "";
  background: #f5f8f8;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: block;
  clip-path: polygon(0 0, 100% 0%, 100% 75%, 0 100%);
}
.topBannerContainer {
  max-width: 966px;
  padding: 0px 15px;
  margin: 0px auto;
  position: relative;
}

.topBannerTitle h3 {
  font-weight: 400;
  font-size: 40px;
  line-height: 104.5%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #333333;
  margin: 0;
  padding-bottom: 20px;
}

.topBannerTitle h3 span {
  color: #f35c5c;
}

.topBannerTitle p {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: -0.01em;
  margin: 0;
  padding-bottom: 35px;
  color: #333333;
}

.formGrid {
  background: #ffffff;
  border: 2px solid rgba(98, 67, 206, 0.18);
  box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
  border-radius: 30px;
  padding: 50px;
  max-width: 584px;
  margin: 0px auto;
}

.formGrid ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
  gap: 36px;
  padding-bottom: 20px;
}

.formGrid ul li.active svg path {
  fill: #6243ce;
}
.formGrid ul li:nth-child(4).active svg circle,
.formGrid ul li:nth-child(4).active svg circle ~ path {
  stroke: #6243ce;
  fill: inherit;
}
.formGrid ul li:nth-child(5).active svg path,
.formGrid ul li:nth-child(5).active svg rect,
.formGrid ul li:nth-child(5).active svg line {
  stroke: #6243ce;
  fill: inherit;
}
.formGrid ul li:nth-child(5).active svg rect + path {
  fill: #6243ce;
  stroke: inherit;
}

.formGrid ul li:nth-child(6).active svg path {
  fill: #6243ce;
}
.formGrid ul li:nth-child(7).active svg path {
  stroke: #6243ce;
  fill: inherit;
}
.formGrid ul li:nth-child(6).active svg path:last-child {
  stroke: #6243ce;
  fill: inherit;
}
.formGrid ul li:nth-child(7).active svg path ~ path {
  stroke: inherit;
  fill: #6243ce;
}
.formGrid h4 {
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.01em;
  color: #333333;
  padding-bottom: 15px;
  font-size: 20px;
  line-height: 120%;
  margin: 0;
}

.formGrid h4 img {
  position: relative;
  top: 9px;
}

.formGrid h4 span {
  color: #6243ce;
}

.formGroup {
  margin-bottom: 20px;
}
.formGroup label {
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  letter-spacing: 0.01em;
  color: #333333;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 15px;
  padding-bottom: 15px;
  text-align: left;
  font-family: "DM Serif Display", serif;
  transition: all 0.3s;
  position: relative;
  z-index: 9;
}

.formInfoIcon {
  position: relative;
  display: flex;
  cursor: pointer;
}

.formInfoTooltip {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(12%, 0%);
  flex-direction: column;
  justify-content: center;
  padding: 15px;
  width: 220px;
  min-height: 38px;
  background: #ffffff;
  border: 1px solid #ced0d0;
  box-shadow: 0px 8px 24px -4px rgb(27 46 94 / 8%);
  border-radius: 8px;
  font-family: "Raleway";
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  display: flex;
  align-items: center;
  letter-spacing: -0.01em;
  font-feature-settings: "calt" off;
  color: #333333;
  user-select: none;
  z-index: 9;
  opacity: 0;
  visibility: hidden;
  cursor: help;
  transition: all 0.3s;
}

.formInfoIcon:hover .formInfoTooltip {
  opacity: 1;
  visibility: visible;
}

.formScroll .formGroup:last-child {
  margin: 0;
}

.border-red {
  border-color: #ff0000;
}

.buttonGrid {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.backBtn {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  border: none;
  letter-spacing: -0.01em;
  background: transparent;
  color: #ced0d0;
  padding: 12px 15px;
  cursor: pointer;
}

.backBtn img {
  margin-right: 10px;
}

.submitBtn {
  background: #f35c5c;
  border-radius: 53px;
  max-width: 206px;
  width: 100%;
  padding: 12px 15px;
  color: #fff;
  border: 1px solid #f35c5c;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.02em;
  color: #ffffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.submitBtn img {
  margin-left: 10px;
}

.formScroll {
  /* height: 175px;
    overflow-y: scroll;
    overflow-x: hidden; */
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  transition: all 0.3s;
  position: relative;
  z-index: 9;
}

.formScroll.formFadeOut {
  /* animation: fadeout 400s linear 0s 1 normal none; */
  opacity: 0.4;
  transform: translateY(20px);
}

.formScroll.formFadeIn {
  /* animation: fadeout 400s linear 0s 1 normal none; */
  opacity: 1;
  transform: translateY(0px);
}

.formScroll .formFadeOutLeft {
  opacity: 0.2;
  transform: translateY(5px);
}

.formScroll .formFadeInLeft {
  opacity: 1;
  transform: translateY(0px);
}

/* @keyframes fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
} */

.formField:nth-child(2) {
  opacity: 0.4;
  pointer-events: none;
}

.formScroll::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

.formScroll::-webkit-scrollbar {
  width: 0px;
  background-color: #f5f5f5;
}

.formScroll::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}

/************************* How It Works ***************************/

.howItWork {
  position: relative;
}

.howItWorkContainer {
  max-width: 1070px;
  padding: 0px 15px;
  margin: 0px auto;
}

.howItWork h3 {
  font-weight: 400;
  font-size: 33px;
  line-height: 120%;
  padding-bottom: 70px;
  text-align: center;
  letter-spacing: 0.01em;
  margin: 0;
  color: #333333;
}

.howItWorkGrid {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 30px;
}

.howItWorkCard {
  background: #ffffff;
  box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
  border-radius: 10px;
  padding: 40px 20px;
  text-align: center;
}

.howItWorkCard h4 {
  margin: 0;
  padding: 20px 0px 10px;
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  letter-spacing: 0.01em;
  color: #333333;
}

.howItWorkCard p {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  letter-spacing: -0.01em;
  margin: 0;
  color: #333333;
}

.howItWorkCard p span {
  font-weight: 700;
}

/************************* Testimonials ***************************/

.testimonial {
  position: relative;
  padding-top: 100px;
}

.testimonialContainer {
  max-width: 1290px;
  margin: 0px auto;
  padding: 0px 15px;
}
.testimonial h3 {
  font-weight: 400;
  font-size: 33px;
  line-height: 120%;
  padding-bottom: 70px;
  text-align: center;
  letter-spacing: 0.01em;
  margin: 0;
  color: #333333;
}

.testimonialGrid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 30px;
}

.testimonialCard {
  background: #fff5f5;
  border-radius: 11px;
  padding: 30px 20px;
  position: relative;
}
.testimonialCard:after {
  content: "";
  width: 0;
  height: 0;
  border-top: 30px solid transparent;
  border-bottom: 30px solid transparent;
  border-right: 30px solid #fff5f5;
  position: absolute;
  bottom: -30px;
  right: -4px;
  transform: rotate(-225deg);
  border-top-right-radius: 40px;
}

.testimonialCard p {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.01em;
  font-feature-settings: "calt" off;
  color: #333333;
  opacity: 0.7;
  margin: 0;
  padding: 20px 0px 23px;
}

.testimonialCard h4 {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  letter-spacing: 0.01em;
  color: #333333;

  display: flex;
  align-items: center;
}

.testimonialCard h4 img {
  margin-right: 10px;
}

.rating {
  display: flex;
  gap: 6px;
}
/************************* Call To Action *************************/

.callToAction {
  position: relative;
  padding: 100px 0px;
}

.callToActionContainer {
  max-width: 1166px;
  margin: 0px auto;
  padding: 0px 15px;
}

.callToActionGrid {
  background: #f5f8f8;
  border-radius: 30px;
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 100px;
}

.callToActionCover {
  max-width: 474px;
  width: 100%;
  position: relative;
}

.callToActionCover video {
  width: 100%;
}

.callToActionCover img {
  width: 100%;
}

.callToActionContent h2 {
  font-weight: 400;
  font-size: 33px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #333333;
  max-width: 340px;
  text-align: center;
  margin: 0;
  padding-bottom: 34px;
  margin: 0px auto;
}

.callToActionContent h2 span {
  color: #6243ce;
}

.callToActionContent > span {
  background: #f35c5c;
  box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.12),
    0px 2px 4px -1px rgba(0, 0, 0, 0.07);
  border-radius: 74px;
  padding: 16px 32px;
  max-width: 190px;
  width: 100%;
  margin: 0px auto;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.02em;
  display: block;
  text-align: center;
  text-decoration: none;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.3s;
}

.callToActionContent > span:hover {
  background: #f35c5c;
}

.typoAnimate {
  border-radius: 10px;
  background: #fff;
  max-width: 223px;
  height: 43px;
  position: absolute;
  right: -70px;
  top: 25px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.typoAnimate {
  color: #6243ce;
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  text-decoration: none;
  font-family: "DM Serif Display", serif;
}

/****************** Media Query *********************/

@media screen and (max-width: 991px) {
  .topBanner {
    padding: 40px 0px 64px;
  }

  .topBanner:before {
    content: "";
    bottom: 120px;
    clip-path: polygon(0 0, 100% 0%, 100% 90%, 0 100%);
  }

  .topBannerTitle h3 {
    font-size: 25px;
    line-height: 104.5%;
    padding-bottom: 10px;
  }

  .topBannerTitle p {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 24px;
  }

  .formGroup label {
    font-size: 18px;
    line-height: 120%;
  }

  .backBtn,
  .submitBtn {
    font-size: 14px;
  }

  .formGrid {
    padding: 30px 20px;
    border: 2px solid #ffffff;
    box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
    border-radius: 20px;
  }

  .formScroll {
    margin-bottom: 18px;
  }

  .formGrid h4 {
    font-size: 17px;
    line-height: 120%;
  }

  .formGrid h4 img {
    display: none;
  }
  .formGrid ul {
    gap: 20px;
    padding-bottom: 20px;
    flex-wrap: wrap;
  }

  .howItWorkGrid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 10px;
  }

  .howItWorkContainer {
    padding: 0px 50px;
  }
  .howItWork h3 {
    padding-bottom: 30px;
    font-size: 25px;
    line-height: 120%;
  }
  .howItWorkCard {
    padding: 40px 35px;
  }

  .testimonialGrid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .testimonialCard p {
    font-size: 14px;
    line-height: 140%;
  }

  .testimonial {
    padding-top: 46px;
  }

  .testimonialContainer {
    padding: 0px 50px;
  }

  .testimonial h3 {
    padding-bottom: 30px;
    font-size: 25px;
    line-height: 120%;
  }

  .testimonialCard {
    padding: 16px 20px;
  }

  .callToAction {
    padding: 63px 0px 37px;
  }

  .callToActionGrid {
    flex-wrap: wrap;
    padding: 30px 15px 0px;
    gap: 5px;
  }
  .callToActionContent {
    width: 100%;
    order: 1;
  }

  .callToActionContent h2 {
    font-size: 25px;
    line-height: 120%;
    padding-bottom: 20px;
  }
  .callToActionCover {
    order: 2;
  }
}

@media screen and (max-width: 480px) {
  .howItWorkGrid {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 10px;
  }

  .testimonialGrid {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 40px;
  }
  .formGroup label {
    justify-content: center;
    text-align: center;
  }
  .callToActionContent a {
    max-width: 100%;
  }

  .callToActionCover img {
    width: 100%;
    height: auto;
  }

  .typoAnimate {
    font-size: 14px;
  }

  .typoAnimate {
    max-width: 140px;
    height: 30px;
    right: -10px;
  }

  .callToActionContent > span {
    padding: 12px 32px;
    max-width: 100%;
  }
  .formInfoTooltip {
    padding: 10px;
    font-size: 14px;
    line-height: 20px;
    top: -50%;
    left: unset;
    right: 0;
    transform: translate(3%, -100%);
  }
  .topBannerTitle h3,
  .topBannerTitle p {
    max-width: 320px;
    width: 100%;
    margin: 0 auto;
  }
}
