.bannerContent {
  padding: 50px 0;
  /* background-color: #ebf3ff; */
  background: linear-gradient(141.81deg, #ffffff 19.34%, #e7f0ff 57.65%);
  overflow: hidden;
}
.bannerContainer {
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
}
.bannerContentContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 9999;
}
.bannerContentContainer:before {
  content: "";
  position: absolute;
  top: -30px;
  right: -225px;
  background: url(../../../assets/cdimages/mapp.png);
  width: 1251px;
  height: 725px;
  z-index: -1;
}
.bannerContentLeft {
  z-index: 9999;
  position: relative;
}
.bannerContentLeft h2 {
  font-family: "DM Sans";
  font-weight: 500;
  font-size: 70px;
  line-height: 100%;
  letter-spacing: -0.03em;
  color: #252d43;
  margin: 0;
  padding-bottom: 18px;
}
.bannerContentLeft h2 span {
  position: relative;
}
.bannerContentLeft h2 span img {
  position: absolute;
  bottom: 10px;
}
.bannerContentLeft p {
  font-family: "Inter";
  font-weight: 400;
  font-size: 14.5424px;
  line-height: 140%;
  letter-spacing: -0.03em;
  color: #252d43;
  margin: 0;
  padding-bottom: 35px;
}
.bannerContentLeft button {
  font-family: "DM Sans";
  padding: 15.5px 38.5px;
  background: #ec8525;
  border-radius: 3px;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  letter-spacing: -0.03em;
  color: #ffffff;
  border: none;
  cursor: pointer;
}
.bannerContentRight,
.bannerContentRight img {
  position: relative;
  z-index: 999;
}

/* ****************************************** */

.bannerVersionContent {
  position: relative;
}
.bannerVersionContent img {
  width: 100%;
  position: relative;
}
.bannerContentversion {
  position: absolute;
  top: 50%;
  left: 180px;
