/* ***************************************** */
.formContent {
  padding: 0 0 100px 0;
}
.formContainer {
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
}
.formBg {
  max-width: 1170px;
  width: 100%;
  height: 379px;
  background: url(../../../assets/cdimages/formbg.png) no-repeat center;
  border-radius: 43px;
}
.formBgversion {
  background: url(../../../assets/cdimages/formbgversion.png) no-repeat center;
  border-radius: 43px;
  width: 1170px;
  height: 379px;
}
.formContents {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 45px 0 100px;
}
.formText {
  max-width: 445px;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.formText h4 {
  font-family: "DM Sans";
  font-weight: 500;
  font-size: 32px;
  line-height: 120%;
  letter-spacing: -0.03em;
  text-transform: capitalize;
  color: #ffffff;
  margin: 0;
}
.formText h4 span {
  position: relative;
  display: block;
  z-index: 9;
}
.formText h4 span:before {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  background: url(../../../assets/cdimages/underline.svg) no-repeat center;
  width: 149px;
  height: 8px;
  background-size: cover;
  z-index: -1;
}
.formText p {
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: -0.03em;
  color: #ffffff;
  margin: 0;
  font-family: "Inter";
}
.formMainContent {
  max-width: 425px;
  width: 100%;
  background: #ffffff;
  box-shadow: 0px 23px 27px rgba(0, 0, 0, 0.08);
  border-radius: 43px;
  padding: 30px 20px;
  margin-top: -220px;
  position: relative;
  z-index: 9;
}
.formMainContent:before {
  content: "";
  position: absolute;
  top: -15px;
  left: -30px;
  background: url(../../../assets/cdimages/liness.svg) no-repeat center;
  width: 45px;
  height: 50px;
  background-size: cover;
}
.formFieldGrid form {
  display: grid;
  gap: 8px;
}
.formFieldGrid form input,
.formFieldGrid form input:focus-visible,
.formFieldGrid form input:active,
.formFieldGrid form input:focus {
  padding: 12px 24px;
  /* max-width: 385px !important; */
  /* width: 100%; */
  /* height: 42px; */
  background: #ffffff;
  border: 2px solid #dcdcdc;
  border-radius: 5px;
  color: #000;
  outline: none;
}
.formFieldGrid :global(.react-tel-input) input {
  width: 100%;
}
.formFieldTel {
  padding-left: 45px !important;
}
.formFieldGrid form input::placeholder {
  font-weight: 400;
  font-size: 16px;
  /* line-height: 140%; */
  color: #9b9b9b;
}
.formFieldGrid form :global(.react-tel-input .form-control) {
  width: 100%;
  background: #ffffff;
  border: 2px solid #dcdcdc;
  border-radius: 5px;
  height: 42px;
}
.formFieldGrid :global(.react-tel-input .selected-flag .arrow) {
  border: none;
  background: url(../../../assets/cdimages/arrowcd.svg) no-repeat;
  width: 16px;
  height: 10px;
}
.formFieldGrid :global(.react-select__indicator-separator) {
  display: none !important;
}
.formFieldGrid
  :global(.react-select__indicator.react-select__dropdown-indicator)
  svg {
  fill: #252d43;
}
.formFieldGrid form :global(.react-tel-input .selected-flag) {
  height: 100% !important;
}
.formFieldGrid form :global(.react-tel-input .selected-flag:hover),
.formFieldGrid form :global(.react-tel-input .selected-flag:focus) {
  background-color: transparent;
}
.formFieldGrid button {
  padding: 10px 27px;
  width: 100%;
  height: 45px;
  background: #ec8525;
  border-radius: 3px;
  border: none;
  cursor: pointer;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  letter-spacing: -0.03em;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.formFieldGrid button:disabled {
  cursor: not-allowed;
}
.formFieldGrid button img {
  width: 20px;
}
.formFieldGrid label {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}
.formFieldGrid label input[type="checkbox"] {
  -webkit-appearance: none;
  background-color: transparent;
  border: 2px solid #9b9b9b;
  border-radius: 1px;
  padding: 7px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 5px;
}
input[type="checkbox"]:checked::after {
  content: "";
  display: block;
  position: absolute;
  top: 1px;
  left: 5px;
  width: 4px;
  height: 8px;
  border: solid #9b9b9b;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.formFieldGrid label p,
.formFieldGrid label p a {
  font-family: "Inter";
  font-weight: 400;
  font-size: 14px;
  line-height: 120%;
  letter-spacing: -0.03em;
  color: #838383;
  margin: 0;
}

.react-select__indicator-separator {
  display: none !important;
}
.react-select__indicator.react-select__dropdown-indicator svg {
  fill: #000 !important ;
}
