/* *************************** button ************************* */
.cd-button {
  --background: #ec8525;
  --text: #fff;
  --font-size: 16px;
  --duration: 0.44s;
  --move-hover: -4px;
  --shadow: 0 2px 8px -1px rgba(39, 94, 254, 0.32);
  --shadow-hover: 0 4px 20px -2px rgba(39, 94, 254, 0.5);
  --font-shadow: var(--font-size);
  padding: 13px 27px;
  font-family: "DM Sans";
  font-weight: 500;
  line-height: var(--font-size);
  border-radius: 3px;
  display: block;
  outline: none;
  text-decoration: none;
  font-size: var(--font-size);
  letter-spacing: -0.03em;
  background: var(--background);
  color: var(--text);
  box-shadow: var(--shadow);
  transform: translateY(var(--y)) translateZ(0);
  transition: transform var(--duration) ease, box-shadow var(--duration) ease;
}
.cdButton div {
  display: flex;
  overflow: hidden;
  text-shadow: 0 var(--font-shadow) 0 var(--text);
}
.cdButton div span {
  display: block;
  backface-visibility: hidden;
  font-style: normal;
  transition: transform var(--duration) ease;
  transform: translateY(var(--m)) translateZ(0);
  min-width: 4px;
}
.cdButton div span:nth-child(1) {
  transition-delay: 0.05s;
}
.cdButton div span:nth-child(2) {
  transition-delay: 0.1s;
}
.cdButton div span:nth-child(3) {
  transition-delay: 0.15s;
}
.cdButton div span:nth-child(4) {
  transition-delay: 0.2s;
}
.cdButton div span:nth-child(5) {
  transition-delay: 0.25s;
}
.cdButton div span:nth-child(6) {
  transition-delay: 0.3s;
}
.cdButton div span:nth-child(7) {
  transition-delay: 0.35s;
}
.cdButton div span:nth-child(8) {
  transition-delay: 0.4s;
}
.cdButton div span:nth-child(9) {
  transition-delay: 0.45s;
}
.cdButton div span:nth-child(10) {
  transition-delay: 0.5s;
}
.cdButton div span:nth-child(11) {
  transition-delay: 0.55s;
}
.cdButton div span:nth-child(12) {
  transition-delay: 0.6s;
}
.cdButton div span:nth-child(13) {
  transition-delay: 0.65s;
}
.cdButton div span:nth-child(14) {
  transition-delay: 0.7s;
}
.cdButton div span:nth-child(15) {
  transition-delay: 0.75s;
}
.cdButton div span:nth-child(16) {
  transition-delay: 0.8s;
}
.cdButton div span:nth-child(17) {
  transition-delay: 0.85s;
}
.cdButton div span:nth-child(18) {
  transition-delay: 0.9s;
}
.cdButton div span:nth-child(19) {
  transition-delay: 0.95s;
}
.cdButton div span:nth-child(20) {
  transition-delay: 1s;
}
.cdButton div span:nth-child(21) {
  transition-delay: 1.05s;
}
.cdButton div span:nth-child(22) {
  transition-delay: 1.1s;
}
.cdButton div span:nth-child(23) {
  transition-delay: 1.15s;
}
.cdButton div span:nth-child(24) {
  transition-delay: 1.2s;
}
.cdButton div span:nth-child(25) {
  transition-delay: 1.25s;
}
.cdButton:hover {
  --y: var(--move-hover);
  --shadow: var(--shadow-hover);
}
.cdButton:hover span {
  --m: calc(var(--font-size) * -1);
}

/****************** Media Query *********************/

@media screen and (max-width: 991px) {
  .bannerContainer,
  .serviceContentContainer,
  .formContainer {
    width: inherit;
    margin: 0 20px;
  }
  .bannerContentRight img {
    max-width: 400px;
    width: 100%;
  }
  .bannerContentContainer:before {
    top: -68px;
    right: 0px;
    width: 100%;
    height: 300px;
  }
  .serviceContentBox {
    padding-top: 120px;
  }
  .serviceContentBoxItem {
    max-width: 165px;
  }
  .formContents {
    /* flex-wrap: wrap; */
  }

  .serviceContentBoxItem:nth-child(2),
  .serviceContentBoxItem:nth-child(4),
  .serviceContentBoxItem:nth-child(3) {
    margin-bottom: 0;
  }
  .formContents {
    padding: 0 40px;
  }
  .formMainContent {
    margin-top: -100px;
  }
  .formBg,
  .formBgversion {
    width: 100%;
    /* height: 100%; */
  }
  .formText h4 span {
    display: inline-block;
  }
  .reviewSliderBtn {
    left: 20px;
  }
}
@media screen and (max-width: 767px) {
  .bannerContainer {
    margin: 0;
  }

  .bannerContentContainer {
    flex-wrap: wrap;
    padding: 0 20px;
    gap: 20px;
  }
  .bannerContentContainer:before {
    height: 100%;
  }
  .bannerContentLeft h2 {
    font-size: 62px;
  }
  .serviceContentLeft h2 span {
    display: inline-block;
  }
  .serviceContentBox {
    flex-wrap: wrap;
    gap: 20px 0;
  }
  .serviceContentBoxItem:nth-child(4)::after {
    top: -56px;
    left: -60px;
  }
  .formContents {
    flex-wrap: wrap;
    gap: 20px;
    padding-top: 20px;
  }
  .formContent {
    padding-bottom: 400px;
  }
  .formMainContent {
    margin: 0;
    width: inherit;
  }
  .bannerVersionContent img {
    height: 600px;
    object-fit: cover;
  }
  .bannerContentversion {
    left: 20px;
  }
}
