.cdheader {
  position: relative;
  z-index: 9;
  background: #ffffff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
  padding: 23px 0;
}

.cdcontainer {
  max-width: 1170px;
  margin: 0px auto;
}
.cdheaderContent {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.cdheaderRight ul {
  display: flex;
  align-items: center;
  gap: 56px;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.cdheaderRight ul li {
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  letter-spacing: -0.03em;
  color: #252d43;
  padding: 8px 15px;
  cursor: pointer;
}
.cdheaderRightToggle {
  display: none;
}
@media screen and (max-width: 991px) {
  .cdcontainer {
    margin: 0 20px;
  }
}
@media screen and (max-width: 767px) {
  .cdheaderRight {
    display: none;
  }
  .cdheaderRightToggle {
    display: block;
    /* position: relative; */
    z-index: 9999;
  }
  .cdheaderRightItem {
    position: absolute;
    left: 0;
    top: 85px;
    right: 0;
    background-color: #252d43;
    z-index: 999;
  }
  .cdheaderRightItem ul {
    list-style-type: none;
    display: grid;
    place-items: center;
    gap: 30px;
    padding: 0;
    margin: 40px 0;
  }
  .cdheaderRightItem ul li {
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: -0.03em;
    color: #fff;
  }
  .cdheader {
    z-index: 99999;
  }
}
