.footer {
  position: relative;
  background: #333333;
  padding: 27px 0px;
}

.container {
  max-width: 1150px;
  margin: 0px auto;
  padding: 0px 15px;
}

.footer p {
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  letter-spacing: -0.01em;
  color: #6E6F6F;
  margin: 0;
  font-weight: 500;
}

.footer a {
  text-decoration: none;
  color: #6E6F6F;
  transition: all 0.3s;
}

@media screen and (max-width: 767px) {
  .footer {
    padding: 49px 0px;
  }
}
